
.navigationItem {
    display: inline-block; // w jednej lini
    margin-right: 30px;
    list-style: none;
    font-size: 12px;

    &Link {
        text-decoration: none;
        color: #818181;
    }


}
.nawigationWrapper {
    min-width: 250px;
}

.navigationItemLinkActive {
    font-weight: 700;
}

.header__wrapper {
    display: grid;
    grid-template-columns: 250px 1fr;
    align-items: center;
    padding: 20px 30px;
    ;
}

@media(min-width: 650px) {
    .navigationItem {
        font-size: 16px;
        margin-right: 60px;
    }
    .header__wrapper {
        grid-template-columns: 600px 1fr;
        padding: 30px 40px;
    }
    .secondary {
        font-size: 12px;
    }
}