@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500,700&display=swap);
@-webkit-keyframes appear {
    0% {
        opacity:0;
        top: 35px;
    }
    100% {
        opacity: 1;
        top: 0;
    }
}


@keyframes appear {
    0% {
        opacity:0;
        top: 35px;
    }
    100% {
        opacity: 1;
        top: 0;
    }
}


.listItem__wrapper {
padding: 30px 30px 50px 0;
list-style: none;
padding: 0;
margin: 0 0 50px;
display: flex;
align-items: center;
position: relative;
-webkit-animation: appear .5s ease;
        animation: appear .5s ease;

}

.pudelko {

}

.listItem__image {
    /* nie kurczy sie pod flexem */
    flex-shrink: 0; 
width: 70px;
height: 70px;
margin-right: 30px;
border-radius: 50%;
}

.listItem__description {
    margin: 10px 0 20px;
    font-weight: 300;
    font-size: 12px;
}
.listItem__button {
    padding: 6px 10px;
    font-weight: 500;
    font-size: 8px;
    text-decoration: none;
    background: none;
    border: 2px solid #1E58FF;
    color: #1E58FF;
}

.listItem__name {
    flex-wrap: wrap;
    font-size: 14px;
}


@media(min-width: 650px) {
    .listItem__image {
    width: 130px;
    height: 130px;
    }

    .listItem__description {
        font-size: 14px;
        margin: 15px 0 30px;
    }
    .listItem__button {
        padding: 10px 15px;
        font-size: 12px;
    }

}
.form__button,.secondary{font-size:10px;text-decoration:none;padding:7px 12px;font-weight:500;background:none;border:2px solid #1E58FF;color:#1E58FF;max-width:120px;text-align:center}.secondary{background-color:#1E58FF;color:white;font-size:8px;width:70px;padding:5px 10px}

.listItem__name{margin:0;color:#1E58FF;font-weight:700}

.listWrapper__wrapper {
    width: 80vw;
    margin: 30px 40px 0;

}
.noItems {
    font-size: 1em;
    color: darkgray;
    display: block;
    width: 100%;
    margin-top: 100px;
    text-align: center;
}
.navigationItem{display:inline-block;margin-right:30px;list-style:none;font-size:12px}.navigationItemLink{text-decoration:none;color:#818181}.nawigationWrapper{min-width:250px}.navigationItemLinkActive{font-weight:700}.header__wrapper{display:grid;grid-template-columns:250px 1fr;align-items:center;padding:20px 30px}@media (min-width: 650px){.navigationItem{font-size:16px;margin-right:60px}.header__wrapper{grid-template-columns:600px 1fr;padding:30px 40px}.secondary{font-size:12px}}




.modal__wrapper{top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);left:0;right:0;margin:0 auto;margin:0 20px 0 20px;background-color:white;box-shadow:0px 17px 300px 180px rgba(129,129,129,0.5);position:fixed}@media (min-width: 400px){.modal__wrapper{width:60vw;height:80vh;margin:0 auto}}

.form__wrapper{width:100%;margin:50px auto 50px;display:flex;flex-direction:column;align-items:center;z-index:999}.form__wrapper h2{color:#1E58FF}.form__form{width:100%;max-width:350px;display:flex;flex-direction:column;justify-content:center;align-items:flex-start}.form__form button{margin-top:25px}.label{position:inline-block;font-size:10px}.radio{position:relative;display:inline-flex;align-items:center;margin:20px 20px 0 0;font-weight:700;color:#1e58ff;font-size:10px}.input{margin-right:5px;width:15px;height:15px;border-radius:50%;border:2px solid #1e58ff;font-size:10px}@media (min-width: 650px){.label{font-size:16px}.form__label{font-size:16px}.radio{font-size:13px}}

.form__item{width:100%;margin:25px 0;position:relative;flex-shrink:0}.forms__item__bar{width:100%;height:2px;background:#7d7d7d;-webkit-transition:.1s all;transition:.1s all}.form__label{color:#7d7d7d;position:absolute;top:3px;left:0;-webkit-transition:.2s ease-out all;transition:.2s ease-out all;font-size:10px;margin-left:10px}@media (min-width: 400px){.form__label{font-size:12px}textarea{min-height:120px}}@media (min-width: 600px){.form__label{font-size:14px}textarea{min-height:150px}.listItem__name{font-size:20px}}input,textarea{color:#7d7d7d;font-size:14px;border:none;line-height:22px;width:100%;background:none}textarea{resize:none;min-height:80px}input:focus,textarea:focus{outline:none}input:focus+.form__label,textarea:focus+.form__label{top:-22px;font-size:13px}input:not(:placeholder-shown)+.form__label,textarea:not(:placeholder-shown)+label{top:-22px;font-size:13px}input:focus ~ .forms__item__bar,textarea:focus ~ .forms__item__bar{background:#1E58FF}

* {
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', 'sans-serif';
}
