
.form__item {
    width: 100%;
    margin: 25px 0;
    position: relative;
    flex-shrink: 0;
    
}

.forms__item__bar {
      width: 100%;
      height: 2px;
      background: #7d7d7d;
      transition: .1s all;
    }

.form__label {
    color: #7d7d7d;
    position: absolute;
    top: 3px;
    left: 0;
    transition: .2s ease-out all;
    font-size: 10px;
    margin-left: 10px;
}
@media(min-width: 400px) {
    .form__label {
        font-size: 12px;
    }
    textarea {
        min-height: 120px;
    }
}
   
@media(min-width: 600px) {
    .form__label {
        font-size: 14px;
    }
    textarea {
        min-height: 150px;
    }
    .listItem__name {
        font-size: 20px;
    }
}

input, textarea {
    color: #7d7d7d;
    font-size: 14px;
    border: none;
    line-height: 22px;
    width: 100%;
    background: none;
}
    
textarea {
    resize: none;
    min-height: 80px;
}
    
input:focus, textarea:focus {
    outline: none;
}
    
input:focus + .form__label, textarea:focus + .form__label {
    top: -22px;
    font-size: 13px;
}

input:not(:placeholder-shown) + .form__label, textarea:not(:placeholder-shown) + label  {
    top: -22px;
    font-size: 13px;
}

input:focus ~ .forms__item__bar, textarea:focus ~ .forms__item__bar {
    background: #1E58FF;
}

