@keyframes appear {
    0% {
        opacity:0;
        top: 35px;
    }
    100% {
        opacity: 1;
        top: 0;
    }
}


.listItem__wrapper {
padding: 30px 30px 50px 0;
list-style: none;
padding: 0;
margin: 0 0 50px;
display: flex;
align-items: center;
position: relative;
animation: appear .5s ease;

}

.pudelko {

}

.listItem__image {
    /* nie kurczy sie pod flexem */
    flex-shrink: 0; 
width: 70px;
height: 70px;
margin-right: 30px;
border-radius: 50%;
}

.listItem__description {
    margin: 10px 0 20px;
    font-weight: 300;
    font-size: 12px;
}
.listItem__button {
    padding: 6px 10px;
    font-weight: 500;
    font-size: 8px;
    text-decoration: none;
    background: none;
    border: 2px solid #1E58FF;
    color: #1E58FF;
}

.listItem__name {
    flex-wrap: wrap;
    font-size: 14px;
}


@media(min-width: 650px) {
    .listItem__image {
    width: 130px;
    height: 130px;
    }

    .listItem__description {
        font-size: 14px;
        margin: 15px 0 30px;
    }
    .listItem__button {
        padding: 10px 15px;
        font-size: 12px;
    }

}