
.modal__wrapper {
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    margin: 0 auto;
    margin: 0 20px 0 20px;
    background-color: white;
    box-shadow: 0px 17px 300px 180px rgba(#818181, .5);
    position: fixed;
}

@media(min-width: 400px) {
.modal__wrapper {
    width: 60vw;
    height: 80vh;
    margin: 0 auto;
}
}