.form__wrapper {
    width: 100%;
    margin: 50px auto 50px; ;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 999;
}

.form__wrapper h2 {
    color: #1E58FF;
}

.form__form {
    width: 100%;
    max-width: 350px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;

    button {
        margin-top: 25px;
    }
}


.label {
    position: inline-block;
    font-size: 10px;

}

.radio { // label
    position: relative;
    display: inline-flex;
    align-items: center;
    margin: 20px 20px 0 0;
    font-weight: 700;
    color: #1e58ff;
    font-size: 10px;
  }

.input {
    margin-right: 5px;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    border: 2px solid #1e58ff;
    font-size: 10px;
  }

  @media (min-width: 650px) {
    .label {
    font-size: 16px;
}

.form__label {
    font-size: 16px;
}
.radio { 
    font-size: 13px;
}


  }
